*{
  margin: 0;
  border: 0;
  padding: 0;
}
body{
  background-color: #17142c;
  color: aliceblue;
}
.chat{
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  position: relative;
  width: 100%;
}
.header{
  position: fixed;
  top: 0;
  width: 100%;
  background-color: rgb(71, 58, 146);
  display: flex;
  justify-content: space-between;
  padding: 1em;
}
.header > button{
  margin-right: 1em;
  padding: 0.5em;
}
.header > h1{
  margin-left: 1em;
}
.messages{
  margin-top: 100px;
  margin-bottom: 80px;
  width: 90%;
}

.sendMsg{
  position: fixed;
  bottom: 0;
  width: 100%;
  /* background-color: aqua; */
  height: 50px;
  display: flex;
  justify-content: space-around;
  margin-bottom: 1em;
}
.sendMsg > input{
  width: 80%;
  height: 70%;
  padding: 0.5em;
  border-radius: 5em;
}
.sendMsg > button{
  width: 15%;
  height: 100%;
  border-radius: 5em;
}
.msg{
  /* background-color: aqua; */
  display: grid;
  grid-template-columns: 100px 1fr;
  margin-top: 1em;
}
.sent{
  background-color: rgba(240, 248, 255, 0.171);
}